import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LetakyService } from '../../providers/letaky-service/letaky-service';

import { ModalController } from 'ionic-angular';
import { GalleryModal } from 'ionic-gallery-modal';


@Component({
  selector: 'page-letaky',
  templateUrl: 'list.html',
  providers: [LetakyService]
})
export class LetakyPage {

  letaky: any;
  photos: any;
  modal;

	  
  	constructor(public navCtrl: NavController, public letakyService: LetakyService, public modalCtrl: ModalController){
    
      this.getLetaky();

  	}

    getLetaky() {
      this.letakyService.getLetaky()
      .then(data => {
        this.letaky = data;
        this.letaky = this.letaky.actions;
        this.photos = this.letaky;
        this.setupModal();
      });
    }

    setupModal() {
      var i;
      for(i = 0; i < this.photos.length; i++){
          this.photos[i].url = this.photos[i]['img_url'];
          this.photos[i].index = i;
          delete this.photos[i].img_url;
      }
    }

    openModal(index) {
      this.modal = this.modalCtrl.create(GalleryModal, {
        photos: this.photos,
        initialSlide: index
      });
      this.modal.present();
    }


}



